/**
 *ES6提供了二进制和八进制数值的新的写法，分别用前缀0b（或0B）和0o（或0O）表示。
 * 从ES5开始，在严格模式之中，八进制就不再允许使用前缀0表示，ES6进一步明确，要使用前缀0o表示。
 */
console.log("**************001");
{
    //二进制
    let r = (0b111110111 === 503 );
    console.info(r);// true

    //八进制
    r = (0o767 === 503);
    console.info(r);/// true
}
